[contenteditable] {
  height: 80%;
  max-height: 80%;
  overflow: auto;
  width: calc(100% - 32px);
  background-color: white;
  padding: 16px;
  cursor: pointer;
  font-family: "Roboto", sans-serif;
  font-size: 1.2rem;
  border: 1px solid rgba(0, 0, 0, 0.23);
  border-radius: 4px;
}

[contenteditable]:focus {
  border: none;
  outline: 1px solid #009788;
  box-shadow: 0 0 3px #009788;
}
